.App {
  text-align: center;
  height: 100vh; /* Magic here */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #346751; /*#206A5D; /*#ECDBBA; /*#F2EDD7;*/
}

.App-logo {
  height: 4vmin;
  pointer-events: none;
  color: #ECDBBA; /*#F9F9F9;*/
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 10s linear;
  }
}
